import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import 'sn-app-object-wizard-components';

import backgroundImage from './assets/summary-page-portal.svg';

import styles from './sn-portal-wizard-summary-page.scss';
import { t } from 'sn-translate';

createCustomElement('sn-portal-wizard-summary-page', {
	view: ({ properties: { wizardContext } }, { dispatch }) => {
		const { app } = wizardContext;
		const { ux_experience_name } = app;

		if (typeof app === 'undefined') {
			return null;
		}

		const data = [
			{
				icon: backgroundImage,
				label: t('Portal success'),
				name: t('Portal success')
			},
			{
				label: t('Name'),
				text: ux_experience_name,
				maxCharLength: 22
			}
		];

		return (
			<sn-guided-wizard-page
				onPrimaryClick={() => {
					dispatch('SN_GUIDED_WIZARD#COMPLETE', {
						key: 'sys_ux_page_registry',
						result: {}
					});
				}}
				primary-label={t('Done')}
				max-width="640px">
				<sn-guided-wizard-page-header
					slot="SN_GUIDED_WIZARD_PAGE#HEADER_SLOT"
					runningHeaderText={t('ADD EXPERIENCE')}
					headerText={t('Success! Your experience has been created')}
				/>
				<sn-aow-info-summary data={data} />
			</sn-guided-wizard-page>
		);
	},
	properties: {
		wizardContext: {}
	},
	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED]: ({ dispatch }) => {
			dispatch('STEPPER_BEHAVIOR#COMPLETE_ALL');
		}
	},
	styles
});
