import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import { createHttpEffect } from '@servicenow/ui-effect-http';
import { t } from 'sn-translate';

import '@servicenow/now-input';
import '@servicenow/now-dropdown';
import '@servicenow/now-pill';
import '@servicenow/now-typeahead';

import {
	SN_EXPERIENCE_WIZARD__CREATE_PORTAL,
	SN_GW__REQUEST_VENDOR_PREFIX,
	SN_GW__GET_VENDOR_PREFIX_SUCCEEDED,
	SN_GW__GET_VENDOR_PREFIX_FAILED,
	SN_GW__IS_EXPERIENCE_PATH_UNIQUE_SUCCEEDED,
	SN_GW__IS_EXPERIENCE_PATH_UNIQUE_FAILED,
	SN_GW__IS_EXPERIENCE_PATH_UNIQUE
} from './constants';

export const requestVendorPrefix = createHttpEffect(
	'/api/now/appcreator/app/vendorprefix',
	{
		method: 'GET',
		successActionType: SN_GW__GET_VENDOR_PREFIX_SUCCEEDED,
		errorActionType: SN_GW__GET_VENDOR_PREFIX_FAILED,
		batch: false
	}
);

//This HTTP effect verifies if the experience path provided is unique or not. Returns true if unique, false otherwise
export const isExperiencePathUnique = createHttpEffect(
	'/api/sn_portal_starte_0/portal_starter_experience/is_unique/:experiencePath',
	{
		method: 'GET',
		pathParams: ['experiencePath'],
		successActionType: SN_GW__IS_EXPERIENCE_PATH_UNIQUE_SUCCEEDED,
		errorActionType: SN_GW__IS_EXPERIENCE_PATH_UNIQUE_FAILED,
		batch: false
	}
);

import progressPagePeople from './assets/progress-page-people.png';
import styles from './sn-portal-wizard-details-page.scss';

import {
	validationBehavior,
	rolesDataBehavior,
	runTemplateBehavior
} from '@devsnc/guided-wizard-toolkit';
import { generateStyleToolTipContent } from '../../../helpers/sn-naming-helpers';

const {
	constants: {
		ROLES_DATA_PROPERTY,
		ROLES_DATA_IN_SCOPE_PROPERTY,
		ROLES_DATA_LOADING_PROPERTY
	}
} = rolesDataBehavior;

const {
	helpers: { createValidationBehavior, returnFormValidState }
} = validationBehavior;

const {
	helpers: { runTemplate },
	actionTypes: { RUN_TEMPLATE_SUCCESS, RUN_TEMPLATE_WARNING }
} = runTemplateBehavior;

function formControlUpdateEffect({ action, updateState }) {
	const { name, value } = action.payload;

	updateState({
		operation: 'set',
		path: name,
		value
	});
}

function formValidityUpdateEffect({ state, action, updateState }) {
	const { isFormInvalid } = state;
	const { name, value: isInvalid } = action.payload;

	if (isFormInvalid !== isInvalid && (name === 'name' || name === 'tables')) {
		updateState({ isFormInvalid: isInvalid });
	}
}

createCustomElement('sn-portal-wizard-details-page', {
	view: (state, { dispatch }) => {
		const {
			ux_experience_name,
			ux_short_description,
			isFormValid,
			ux_experience_url,
			vendorPrefix,
			isCreating
		} = state;
		const {
			wizardContext,
			[ROLES_DATA_PROPERTY]: allRoles,
			[ROLES_DATA_LOADING_PROPERTY]: isRolesLoading
		} = state.properties;
		const companyCode = vendorPrefix
			.replace(/x_snc_/, 'now/')
			.replace(/x_/, 'x/')
			.replace(/sn_/, 'now/')
			.replace(/_/, '/');
		const urlmessage = [
			{
				status: 'info',
				content: `/${companyCode || 'now/'}${ux_experience_url}`
			}
		];
		return (
			<sn-guided-wizard-page
				name="portal-basic-info"
				onPrimaryClick={() => {
					dispatch(SN_GW__IS_EXPERIENCE_PATH_UNIQUE, {
						experiencePath: btoa(ux_experience_url)
					});
				}}
				primary-disabled={!isFormValid || isCreating}
				onSecondaryClick={wizardContext.onClose}
				onClose={wizardContext.onClose}
				secondary-label={t('Cancel')}
				runningHeaderText={t('ADD EXPERIENCE')}
				headerText={t("Let's design your Portal")}
				subheaderText={t('This experience needs a name, description and URL.')}
				max-width="640px"
				loading={isCreating}
				loadingContent={{
					runningHeaderText: t('CREATE EXPERIENCE'),
					headerText: t(`Sit tight while we create this experience`),
					subheaderText: t('This may take a few seconds...'),
					image: progressPagePeople
				}}>
				<div className="body-container">
					<div className="left-column">
						<now-input
							name="ux_experience_name"
							value={ux_experience_name}
							placeholder={t('Enter a name')}
							label={t('Name')}
							helperContent={generateStyleToolTipContent(
								t('User-facing name of the experience')
							)}
							size="lg"
							maxlength={255}
							autocomplete="off"
							required
						/>
						<now-input
							name="ux_short_description"
							value={ux_short_description}
							placeholder={t('Enter a description')}
							label={t('Description')}
							helperContent={generateStyleToolTipContent(
								t('Short description of what this experience does.')
							)}
							size="lg"
							autocomplete="off"
						/>
						<now-input
							name="ux_experience_url"
							value={ux_experience_url}
							label={t('URL')}
							helperContent={generateStyleToolTipContent(
								t(
									'Assign a unique URL for this experience for easy sharing and access. We suggested one for you, but you can change it here.'
								)
							)}
							messages={urlmessage}
							placeholder={ux_experience_url}
							size="lg"
							maxlength={255}
							autocomplete="off"
							pattern="[0-9a-zA-Z/_.-]*"
							required
						/>
						<now-typeahead-multi
							className="roles now-m-block-end--xxl"
							name="selected_roles"
							label={t('Roles')}
							placeholder={
								isRolesLoading
									? t('Loading...')
									: t('Choose who can access this experience')
							}
							helperContent={generateStyleToolTipContent(
								t(
									'Specify who can access this experience by choosing one or more roles. If you want to make this accessible to everyone, leave the field blank. You can create custom roles in Security.'
								)
							)}
							disabled={isRolesLoading}
							items={allRoles}
							selected-items={isRolesLoading ? [] : state.selected_roles}
							manage-selected-items
							variant="tertiary"
							search="contains"
							style={{ width: '100%' }}
						/>
					</div>
				</div>
			</sn-guided-wizard-page>
		);
	},
	initialState: {
		isCreating: false,
		ux_experience_name: '',
		ux_short_description: '',
		ux_experience_url: '',
		vendorPrefix: '',
		allRoles: [],
		selected_roles: [],
		isFormValid: false
	},
	transformState: state => {
		const isFormValid = returnFormValidState(state);
		return { ...state, isFormValid };
	},
	properties: {
		wizardContext: { default: {} },
		[ROLES_DATA_PROPERTY]: {
			default: `@wizard/${ROLES_DATA_PROPERTY}`
		},
		[ROLES_DATA_LOADING_PROPERTY]: {
			default: `@wizard/${ROLES_DATA_LOADING_PROPERTY}`
		},
		[ROLES_DATA_IN_SCOPE_PROPERTY]: {
			default: `@wizard/${ROLES_DATA_IN_SCOPE_PROPERTY}`
		}
	},
	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED]: ({ dispatch, properties }) => {
			const { wizardContext } = properties;
			dispatch('STEPPER_BEHAVIOR#PAGE_SET', {
				page: 'sn-portal-wizard-details-page'
			});
			dispatch('NOW_INPUT#VALUE_SET', {
				name: 'ux_experience_name',
				value: wizardContext.app.name
			});
			dispatch(SN_GW__REQUEST_VENDOR_PREFIX);
		},
		[actionTypes.COMPONENT_PROPERTY_CHANGED]: ({
			state,
			action,
			updateState
		}) => {
			const {
				payload: { value, name }
			} = action;
			const { selected_roles } = state;
			if (
				name === ROLES_DATA_IN_SCOPE_PROPERTY &&
				selected_roles.length === 0
			) {
				updateState({ selected_roles: value });
			}
		},
		[SN_GW__REQUEST_VENDOR_PREFIX]: requestVendorPrefix,
		[SN_GW__GET_VENDOR_PREFIX_SUCCEEDED]: ({
			action: { payload },
			dispatch,
			updateState
		}) => {
			if (payload.result === 'x_') {
				dispatch('SN_GUIDED_WIZARD#REQUEST_WARNING', {
					detail: t(
						'Company code is not set. Please set company code and try again.'
					)
				});
			}
			updateState({ vendorPrefix: payload.result });
		},
		[SN_GW__GET_VENDOR_PREFIX_FAILED]: ({ action: { payload }, dispatch }) => {
			dispatch('SN_GUIDED_WIZARD#CONTEXT_SET', { error: { ...payload } });
		},
		['NOW_INPUT#VALUE_SET']: ({
			updateState,
			action: { payload, stopPropagation }
		}) => {
			stopPropagation();
			const { value, name } = payload;
			updateState({ [name]: value });
			let newUrl = value
				.trim()
				// remove all invalid characters
				.replace(/[^A-Z0-9\s/-]/gi, '')
				// replace whitespaces with a dash
				.replace(/\s+/g, '-')
				// STRY51796576: max of one parts allowed limitation in uxf
				.replace('/', 'PLACEHOLDER')
				.replace(/\//g, '-')
				.replace('PLACEHOLDER', '/')
				.toLowerCase();
			if (name === 'ux_experience_name' || name === 'ux_experience_url') {
				updateState({ ux_experience_url: newUrl });
			}
		},
		'NOW_PILL#DISMISSED': ({
			action: {
				payload: { index }
			},
			state,
			updateState
		}) => {
			const newState = [...state.selected_roles];
			newState.splice(index, 1);
			updateState({ selected_roles: newState });
		},
		['NOW_TYPEAHEAD_MULTI#SELECTED_ITEMS_SET']: formControlUpdateEffect,
		['NOW_TYPEAHEAD_MULTI#INVALID_SET']: formValidityUpdateEffect,
		[SN_EXPERIENCE_WIZARD__CREATE_PORTAL]: ({
			properties: {
				wizardContext: { templateId }
			},
			dispatch,
			state: {
				ux_experience_name,
				ux_short_description,
				ux_experience_url,
				selected_roles
			},
			updateState
		}) => {
			const appData = {
				application_name: ux_experience_name, // Defaulting the name of the sys_app to the name entered above
				ux_experience_name,
				ux_short_description,
				ux_experience_url,
				selected_roles
			};
			runTemplate(dispatch, templateId, appData);
			updateState({ isCreating: true });
		},
		[RUN_TEMPLATE_SUCCESS]: ({
			state: { ux_experience_name },
			action: {
				payload: { result }
			},
			dispatch,
			updateState
		}) => {
			const outputForApp = result.outputs.find(r => r.name === 'app_sys_id');

			if (!outputForApp.value) {
				console.warn('No output for application found');
				return;
			}

			const app = {};
			app.sys_id = outputForApp.value;
			app.ux_experience_name = ux_experience_name;
			updateState({ app }); // Set the app on state for a later chained action

			dispatch('SN_GUIDED_WIZARD#CONTINUE', { app });
		},
		[RUN_TEMPLATE_WARNING]: ({ updateState }) => {
			// The only case where we will "reset" and allow end users to retry with different input
			updateState({ isCreating: false });
		},
		[SN_GW__IS_EXPERIENCE_PATH_UNIQUE]: isExperiencePathUnique,
		[SN_GW__IS_EXPERIENCE_PATH_UNIQUE_SUCCEEDED]: ({
			action: {
				payload: {
					result: { isUnique }
				}
			},
			dispatch
		}) => {
			if (isUnique) {
				dispatch(SN_EXPERIENCE_WIZARD__CREATE_PORTAL);
			} else {
				dispatch('SN_GUIDED_WIZARD#REQUEST_WARNING', {
					detail: t('This URL already exists. Please try another.')
				});
			}
		},
		[SN_GW__IS_EXPERIENCE_PATH_UNIQUE_FAILED]: ({
			action: { payload },
			dispatch
		}) => {
			dispatch('SN_GUIDED_WIZARD#CONTEXT_SET', { error: { ...payload } });
		}
	},
	styles,
	behaviors: [
		createValidationBehavior(['ux_experience_name', 'ux_experience_url']),
		runTemplateBehavior.behavior
	]
});
