import { createCustomElement } from '@servicenow/ui-core';
import { t } from 'sn-translate';
import { getCurrentPage, stepperBehavior } from '@devsnc/guided-wizard-toolkit';
import styles from './sn-portal-starter-wizard.scss';

import './pages/sn-portal-wizard-details-page';
import './pages/sn-portal-wizard-summary-page';

const DETAILS_PAGE = 'sn-portal-wizard-details-page';
const SUMMARY_PAGE = 'sn-portal-wizard-summary-page';

export const applicationFlow = {
	startPage: DETAILS_PAGE,
	pages: {
		[DETAILS_PAGE]: {
			continue: () => SUMMARY_PAGE
		},
		[SUMMARY_PAGE]: {
			previous: () => DETAILS_PAGE
		}
	}
};

const STEPPER_ITEMS = {
	[DETAILS_PAGE]: t('Basic info'),
	[SUMMARY_PAGE]: t('Summary')
};

createCustomElement('sn-portal-starter-wizard', {
	view: ({ properties }, { dispatch }) => {
		const { wizardContext, stepperItems, templateId, app } = properties;
		const PageComponent = getCurrentPage(wizardContext);
		return (
			<sn-guided-wizard
				flowDefinition={applicationFlow}
				component-id="wizard"
				stepper-items={stepperItems}
				initial-context={{
					templateId,
					app,
					onClose: () => dispatch('SN_GUIDED_WIZARD#CANCEL')
				}}
				roles-data-options={{
					appId: app.sys_id,
					fetchOnBootstrap: true,
					normalizeResult: result => {
						return result.map(role => {
							return {
								id: role.sys_id,
								label: role.name,
								description: role.description
							};
						});
					}
				}}>
				<PageComponent wizard-context={wizardContext} />
			</sn-guided-wizard>
		);
	},
	properties: {
		wizardContext: { default: '@wizard/wizardContext' },
		templateId: { default: null },
		app: { default: {} },
		uxf_data: {}
	},
	behaviors: [
		{
			behavior: stepperBehavior.behavior,
			options: {
				config: STEPPER_ITEMS
			}
		}
	],
	styles
});
