import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import '@servicenow/now-loader';
import styles from './sn-workspace-wizard-data-page.scss';
import { t } from 'sn-translate';
import 'sn-app-object-wizard-components';

import backgroundImage from './assets/image_workspace.png';
import loadingImage from './assets/progress-page-people.png';

import {
	SN_EXPERIENCE_WIZARD__CREATE_WORKSPACE,
	SN_EXPERIENCE_WIZARD__FILTERED_TABLES_SET,
	DEFAULT_PRIMARY_TABLE
} from './constants';

import {
	contextHelpers,
	tablesDataBehavior,
	runTemplateBehavior
} from '@devsnc/guided-wizard-toolkit';
import { generateStyleToolTipContent } from '../../../helpers/sn-naming-helpers';

const {
	constants: {
		TABLES_DATA_PROPERTY,
		TABLES_DATA_IN_SCOPE_PROPERTY,
		TABLES_DATA_LOADING_PROPERTY
	}
} = tablesDataBehavior;

const {
	helpers: { runTemplate },
	actionTypes: { RUN_TEMPLATE_SUCCESS, RUN_TEMPLATE_WARNING }
} = runTemplateBehavior;

const { getStateFromContext, getContextFromState } = contextHelpers({
	namespace: 'workspace-data-page',
	keys: ['primary_table', 'secondary_tables']
});

function formControlUpdateEffect({ action, updateState }) {
	const { name, value } = action.payload;
	updateState({
		operation: 'set',
		path: name,
		value
	});
}

function formValidityUpdateEffect({ state, action, updateState }) {
	const { isFormInvalid } = state;
	const { name, value: isInvalid } = action.payload;

	if (isFormInvalid !== isInvalid && (name === 'name' || name === 'tables')) {
		updateState({ isFormInvalid: isInvalid });
	}
}

createCustomElement('sn-workspace-wizard-data-page', {
	view: (state, { dispatch }) => {
		const {
			filtered_tables,
			primary_table,
			primary_table_label,
			secondary_tables,
			isCreating
		} = state;
		const {
			wizardContext,
			[TABLES_DATA_PROPERTY]: allTables,
			[TABLES_DATA_LOADING_PROPERTY]: isTablesLoading
		} = state.properties;
		function onPrimaryClick() {
			dispatch(SN_EXPERIENCE_WIZARD__CREATE_WORKSPACE, {
				primary_table: primary_table,
				secondary_tables: secondary_tables
			});
		}
		return (
			<sn-guided-wizard-page
				onPrimaryClick={onPrimaryClick}
				primary-disabled={!state.primary_table || isCreating}
				onPrevious={() => {
					const newContext = getContextFromState(state, wizardContext);
					dispatch('SN_GUIDED_WIZARD#PREVIOUS', newContext);
				}}
				onClose={() => dispatch('SN_GUIDED_WIZARD#CANCEL')}
				running-header-text={t('ADD EXPERIENCE')}
				header-text={t("Great. Let's add some data.")}
				subheader-text={t(
					'Select the data tables you want to work on in this experience.'
				)}
				max-width="640px"
				loading={isCreating}
				loadingContent={{
					runningHeaderText: t('CREATE EXPERIENCE'),
					headerText: t(`Sit tight while we create this experience`),
					subheaderText: t('This may take a few seconds...'),
					image: loadingImage
				}}>
				<div className="body">
					<div className="left-body">
						<div className="inputs">
							<sn-aow-typeahead
								name="primary_table"
								label={t('Primary Table')}
								placeholder={
									isTablesLoading ? t('Loading...') : t('Search for a table')
								}
								value={primary_table_label || ''}
								helperContent={generateStyleToolTipContent(t('Primary table'))}
								disabled={isTablesLoading}
								items={allTables}
								selected-item={isTablesLoading ? [] : primary_table}
								variant="tertiary"
								search="contains"
								required
								itemsLimit={100}
								style={{ width: '100%' }}
							/>
							<sn-aow-typeahead
								className="secondary-tables"
								name="secondary_tables"
								label={t('Secondary Tables')}
								placeholder={
									isTablesLoading ? t('Loading...') : t('Search for a table')
								}
								helperContent={generateStyleToolTipContent(
									t('Secondary tables')
								)}
								disabled={isTablesLoading}
								items={filtered_tables}
								selected-items={isTablesLoading ? [] : secondary_tables}
								manage-selected-items
								variant="tertiary"
								search="contains"
								itemsLimit={100}
								style={{ width: '100%' }}
								multi
							/>
						</div>
					</div>
					<img className="image" src={backgroundImage} alt="" />
				</div>
			</sn-guided-wizard-page>
		);
	},
	initialState: {
		isCreating: false,
		allTables: [],
		primary_table: DEFAULT_PRIMARY_TABLE.id,
		primary_table_label: DEFAULT_PRIMARY_TABLE.label,
		secondary_tables: [],
		filtered_tables: [],
		isFetching: false
	},
	properties: {
		wizardContext: { default: {} },
		[TABLES_DATA_PROPERTY]: {
			default: `@wizard/${TABLES_DATA_PROPERTY}`
		},
		[TABLES_DATA_IN_SCOPE_PROPERTY]: {
			default: `@wizard/${TABLES_DATA_IN_SCOPE_PROPERTY}`
		},
		[TABLES_DATA_LOADING_PROPERTY]: {
			default: `@wizard/${TABLES_DATA_LOADING_PROPERTY}`
		}
	},
	actionHandlers: {
		[actionTypes.COMPONENT_BOOTSTRAPPED]: ({
			properties,
			dispatch,
			updateState
		}) => {
			const {
				wizardContext,
				[TABLES_DATA_IN_SCOPE_PROPERTY]: scopeTables
			} = properties;
			// Send to the current page to the stepper behavior
			dispatch('STEPPER_BEHAVIOR#PAGE_SET', {
				page: 'sn-workspace-wizard-starter-data-page'
			});

			if (scopeTables && scopeTables.length > 0) {
				const secondary_tables = scopeTables.filter(
					t => t.id !== scopeTables[0].id
				);

				updateState({
					primary_table: scopeTables[0].id,
					primary_table_label: scopeTables[0].label,
					secondary_tables
				});

				dispatch(SN_EXPERIENCE_WIZARD__FILTERED_TABLES_SET, {
					value: scopeTables[0].id
				});
			} else {
				dispatch(SN_EXPERIENCE_WIZARD__FILTERED_TABLES_SET, {
					value: DEFAULT_PRIMARY_TABLE.id
				});
			}
			// Populate state with wizardContext so inputs are persisted when
			// coming back to this page
			const initState = getStateFromContext(wizardContext);
			updateState(initState);
		},
		[actionTypes.COMPONENT_PROPERTY_CHANGED]: ({
			state,
			action,
			updateState
		}) => {
			const {
				payload: { value, name }
			} = action;
			const { tables } = state;
			if (name === TABLES_DATA_IN_SCOPE_PROPERTY && tables.length === 0) {
				updateState({ tables: value });
			}
		},
		'NOW_PILL#DISMISSED': ({
			action: {
				payload: { index }
			},
			state,
			updateState
		}) => {
			const newState = [...state.secondary_tables];
			newState.splice(index, 1);
			updateState({ secondary_tables: newState });
		},
		['NOW_TYPEAHEAD_MULTI#SELECTED_ITEMS_SET']: formControlUpdateEffect,
		['NOW_TYPEAHEAD_MULTI#INVALID_SET']: formValidityUpdateEffect,
		['NOW_TYPEAHEAD#SELECTED_ITEM_SET']: coeffects => {
			const {
				state,
				updateState,
				action: { payload },
				dispatch
			} = coeffects;
			const { value, item } = payload;
			const { secondary_tables } = state;
			const updatedSecondaryTables = secondary_tables.filter(
				t => t.id !== value
			);
			updateState({
				primary_table: value,
				primary_table_label: item.label,
				secondary_tables: updatedSecondaryTables
			});
			dispatch(SN_EXPERIENCE_WIZARD__FILTERED_TABLES_SET, { value });
		},
		[SN_EXPERIENCE_WIZARD__FILTERED_TABLES_SET]: coeffects => {
			const {
				properties,
				updateState,
				action: { payload }
			} = coeffects;
			const { value } = payload;
			const { [TABLES_DATA_PROPERTY]: allTables } = properties;
			const filtered_tables = allTables.filter(table => table.id !== value);
			updateState({
				filtered_tables
			});
		},
		[SN_EXPERIENCE_WIZARD__CREATE_WORKSPACE]: ({
			properties: {
				wizardContext: {
					app,
					templateId,
					'workspace-details-page': {
						ux_experience_name,
						ux_short_description,
						ux_experience_url,
						selected_roles
					}
				}
			},
			action: {
				payload: { primary_table, secondary_tables }
			},
			dispatch,
			updateState
		}) => {
			const appData = {
				application_name: app.name, // Defaulting the name of the sys_app to the name entered above
				ux_experience_name,
				ux_short_description,
				ux_experience_url,
				selected_roles: selected_roles,
				primary_table,
				secondary_tables: secondary_tables.map(t => t.id)
			};
			runTemplate(dispatch, templateId, appData);
			updateState({ isCreating: true });
		},
		[RUN_TEMPLATE_SUCCESS]: ({
			properties,
			action: {
				payload: { result }
			},
			dispatch,
			updateState
		}) => {
			const outputForApp = result.outputs.find(r => r.name === 'app_sys_id');

			if (!outputForApp.value) {
				console.warn('No output for application found');
				return;
			}

			const app = {};
			app.sys_id = outputForApp.value;
			app.ux_experience_name =
				properties.wizardContext['workspace-details-page'].ux_experience_name;
			updateState({ app }); // Set the app on state for a later chained action

			dispatch('SN_GUIDED_WIZARD#CONTINUE', { app });
		},
		[RUN_TEMPLATE_WARNING]: ({ updateState }) => {
			// The only case where we will "reset" and allow end users to retry with different input
			updateState({ isCreating: false });
		}
	},
	styles,
	behaviors: [runTemplateBehavior.behavior]
});
